import userChatBottom from "../../components/user-chat/user-chat-bottom.vue"
import detailInfo from "../../components/detail/detail-info.vue"
import time from "../../common/time.js"
import commentList from "../../components/detail/comment-list.vue"
import moreShare from "../../components/common/more-share.vue"
export default {
	name: "detail",
	components: {
		detailInfo,
		commentList,
		userChatBottom,
		moreShare
	},
	data() {
		return {
			reply_id: 0, 	//回复id
			focus: false,   //评论框是否是选中状态
			sharedata:{
				title: '',
				url: '',
				titlepic: '',
				shareType: 0
			},
			comment: {
				count: 0,
				list: []
			},
			detail: {
				userpic: "",
				username: "",
				sex: 0, // 0男，1女
				age: 0,
				content: '',
				isguanzhu: false,
				title: "",
				titlepic: "",
				morepic: [],
				video: false,
				share: false,
				path: "",
				sharenum: 0,
				commentnum: 0,
				goodnum: 0,
				create_time: 0
			},
			index: 0,
			shareshow: false
		}
	},
	onLoad(e) {
		this.initdata(JSON.parse(e.detailData));
	},
	onNavigationBarButtonTap(e) {
		// console.log(e.index);
		if (e.index == 0) {
			this.togle();
			
		}
	},
	methods: {
		updateData(data){
			switch (data.type){
				case "guanzhu":
				this.updateGuanZhu(data)
					break;
				case "support":
				this.updateSupport(data);
					break;
				case 'updateComment':
				this.updateComment(data);
					break;
			}
		},
		// 更新关注信息
		updateGuanZhu(data){
			this.detail.isguanzhu = data.data;
		},
		updateSupport(data){
			if (data.do == 'ding') {
				this.detail.infonum.index = 1;
				this.detail.goodnum++;
			}
		},
		//初始化分享
		__initShare(obj){
			this.sharedata = {
				title: obj.title,
				content: obj.title,
				url: 'https://ghfree.cn/',
				titlepic: obj.titlepic ? obj.titlepic : 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/app/share-logo@3.png',
				shareType: 0
			}
		},
		//初始化数据
		initdata(obj) {
			//修改窗口标题
			uni.setNavigationBarTitle({title: obj.title});
			//加载分享内容
			this.__initShare(obj);
			//加载中
			uni.showLoading({title: 'Loading...',mask: true});
			obj.morepic = [];
			obj.content = "";
			obj.goodnum = obj.infonum.dingnum;
			this.detail = obj;
			this.comment.count = obj.commentnum;
			//添加文章到浏览历史记录
			this.User.addHistoryList(this.detail);
			//获取文章详情
			this.getdetail();
			if(this.comment.count){
				this.getcomment();
			}
		},
		//请求文章详情
		async getdetail(){
			let [err,res] = await this.$http.get('/post/'+this.detail.id);
			let error = this.$http.errorCheck(err,res,()=>{
				uni.hideLoading();
			},()=>{
				uni.hideLoading();
			})
			if(!error) return;
			let data = res.data.data.detail;
			// console.log(data);
			this.detail.content = data.content;
			let images = [];
			for (var i = 0; i < data.images.length; i++) {
				images.push(data.images[i].url);
			}
			this.detail.morepic = images;
			this.detail.age = data.user.userinfo.age;
			this.detail.sex = data.user.userinfo.sex;
			this.detail.create_time = data.create_time;
			return uni.hideLoading();
		},
		//获取评论
		async getcomment() {
			let [err,res] = await this.$http.get('/post/'+this.detail.id+'/comment');
			if(!this.$http.errorCheck(err,res)) return;
			let list = res.data.data.list;
			this.comment.list = this.comment.list.concat(this.__ArrSort(list));
			// console.log(this.comment.list[0]);
		},
		// 无限级分类
		__ArrSort(arr,id=0){
			// console.log(arr);
			var temp = [],lev=0;
			var forFn = function(arr,id,lev){
				for (let i = 0; i < arr.length; i++) {
					var item = arr[i];
					if(item.fid == id){
						item.lev = lev;
						temp.push({
							id:item.id,
							fid:item.fid,
							userid:item.user.id,
							userpic:item.user.userpic,
							username:item.user.username,
							time:time.gettime.gettime(item.create_time),
							data:item.data,
						});
						forFn(arr,item.id,lev+1);
					}
				}		
			}
			forFn(arr,id,lev);
			return temp;			
		},
		async submit(data) {
			uni.showLoading({title: '评论中...', mask: false});
			let reply_id = this.reply_id;
			let [err,res] = await this.$http.post('/post/comment',{
				post_id:this.detail.id,
				fid:reply_id,
				data:data
			},{
				token: true
			});
			//错误处理
			if(err || res.data.errorCode){
				let msg = res.data.errorCode ? res.data.msg : '发送失败，请检查网络~';
				uni.hideLoading();
				return uni.showToast({
					title: msg, icon: 'none'
				});
			}
			//成功处理
			uni.hideLoading();
			uni.showToast({
				title: '发送成功～'
			});
			let result = {
				id: res.data.data.id,
				fid: reply_id,
				userpic: this.User.userinfo.userpic,
				username: this.User.userinfo.username,
				time: time.gettime.gettime(new Date().getTime()),
				data: data
			}
			// 评论+1
			this.comment.count++;
			this.detail.commentnum++;
			//一级评论
			if(reply_id === 0){
				return this.comment.list.push(result);
			}
			// 二级评论
			// 找出被评论id的索引
			let index = this.comment.list.findIndex( (val) => {
				return val.id === reply_id;
			});
			if(index > -1){
				//中间插入
				this.comment.list.splice(index+1,0,result);
			}
			// 通知首页更新评论数，通知会员中心更新评论数
			uni.$emit('updateData',{
				type:'updateComment',
				post_id:this.detail.id
			})
		},
		// 分享组件显示控制
		togle(){
			this.shareshow=!this.shareshow
		},
		reply(id){
			this.reply_id = id;
			this.focus = true;
			console.log(this.reply_id);
		},
		//直接点击回复框 默认为一级评论(输入框失去焦点执行)
		blur(){
			this.focus = false;
			this.reply_id = 0; //(H5端会一直未一级评论)
			console.log(this.reply_id);
		}
	}
}
